<template>
    <section>

        <!------------------------------------------------------------------------------------------------>
        <transition name="el-zoom-in-bottom">
            <el-col :span="24" class="toolbar" v-show="seniorVisible" style="padding-bottom: 0px;">

                <el-form label-width="120px" :model="filters" ref="seniorFilters">

                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="sql" prop = "sqlSentenceQuery">
                                <el-input v-model="filters.sqlSentenceQuery" placeholder="sql"></el-input>
                            </el-form-item>
                        </el-col>

                        <el-col :span="8">
                            <el-form-item label="" prop = "remarkQuery">
                                <el-input v-model="filters.remarkQuery" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>

                        <el-col :span="8">
                            <el-form-item label="" prop = "creatorQuery">
                                <el-input v-model="filters.creatorQuery" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>


                    </el-row>


                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="" prop = "modifierQuery">
                                <el-input v-model="filters.modifierQuery" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>


                        <el-col :span="8">
                            <el-form-item label="" prop = "createDateRange">
                                <el-date-picker v-model="createDateRange" type="datetimerange" placeholder=""
                                                :editable="false">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>

                        <el-col :span="8">
                            <el-form-item label="" prop = "updateDateRange">
                                <el-date-picker v-model="updateDateRange" type="datetimerange" placeholder=""
                                                :editable="false">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </el-col>
        </transition>
        <!------------------------------------------------------------------------------------------------>


        <!----------------------------------------------------------------------------------------------------->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">

            <el-form :inline="true" :model="filters" ref="filters">
                <el-form-item label="" prop="simpleDataQuery">
                    <el-input v-model="filters.simpleDataQuery" placeholder=""></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" v-on:click="pagingList" :disabled="menuPurview.query" size="small"><i
                            class="fa fa-search fa-lg"></i> 
                    </el-button>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" v-on:click="handleSenior" :disabled="menuPurview.query" size="small"><i
                            :class="searchClass"></i> 
                    </el-button>
                </el-form-item>

                <el-form-item>
                    <el-button type="success" v-on:click="handleReset()" :disabled="menuPurview.query"
                               size="small"><i class="fa fa-refresh fa-lg"></i> 
                    </el-button>
                </el-form-item>

            </el-form>


            <el-form :inline="true">
                <el-form-item>
                    <el-button type="success" @click="handleAdd" :disabled="menuPurview.add" icon="plus" size="small">
                        
                    </el-button>
                </el-form-item>

                <el-form-item>
                    <el-dropdown @command="handleExport">
                        <el-button type="primary" :disabled="menuPurview.export" size="small"><i
                                class="fa fa-cloud-download fa-lg"></i> <i
                                class="el-icon-caret-bottom el-icon--right"></i>
                        </el-button>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="0" :disabled="exportDis"></el-dropdown-item>
                            <el-dropdown-item command="1" :disabled="exportDis"></el-dropdown-item>
                            <el-dropdown-item command="-1" :disabled="exportDis"></el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </el-form-item>
            </el-form>

        </el-col>
        <!----------------------------------------------------------------------------------------------------->


        <!----------------------------------------------sql---------------------------------------------->
        <el-table :data="dataList" :height="tableHigth" v-loading="listLoading" @selection-change="selsChange"
                  highlight-current-row stripe border resizable>
            <el-table-column type="selection"></el-table-column>
            <el-table-column type="index" width="55"></el-table-column>
            <el-table-column prop="sqlSentence" show-overflow-tooltip label="sql" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="remark" show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="creator" show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="modifier" show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="createDate" show-overflow-tooltip label="" :formatter="formatCreateDate"
                             label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="updateDate" show-overflow-tooltip label="" :formatter="formatUpdateDate"
                             label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column label="" width="140" header-align="center" align="center">
                <template slot-scope="scope">
                    <el-button-group>
                        <el-button size="small" type="primary" :disabled="menuPurview.modify"
                                   @click="handleEdit(scope.$index, scope.row)" icon="edit" title=""></el-button>
                        <el-button size="small" type="danger" :disabled="menuPurview.delete"
                                   @click="handleDel(scope.$index, scope.row)" icon="delete" title=""></el-button>
                        <el-button size="small" type="success" :disabled="menuPurview.delete"
                                   @click="handleView(scope.$index, scope.row)" title=""><i class="fa fa-info-circle fa-lg"></i></el-button>
                    </el-button-group>
                </template>
            </el-table-column>
        </el-table>
        <!----------------------------------------------sql---------------------------------------------->


        <!----------------------------------------------------------------------------------------------------->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0 || menuPurview.delete">



            </el-button>
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :page-sizes="[10,15,25,50,100]"
                    :page-size=filters.pageSize
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total"
                    style="float:right;">
            </el-pagination>
        </el-col>
        <!----------------------------------------------------------------------------------------------------->


        <!--------------------------------------------------------------------------------------------------->
        <el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false" size="large">
            <el-form :model="addForm" ref="addForm" label-width="120px" :rules="addFormRules">
                <el-form-item label="sql" prop="sqlSentence">
                    <el-input v-model="addForm.sqlSentence" type="textarea" :rows="5" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="remark">
                    <el-input v-model="addForm.remark" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addFormVisible = false;"></el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
            </div>
        </el-dialog>
        <!--------------------------------------------------------------------------------------------------->


        <!--------------------------------------------------------------------------------------------------->
        <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false" size="large">
            <el-form :model="editForm" ref="editForm" label-width="120px" :rules="editFormRules">
                <el-form-item label="sql" prop="sqlSentence">
                    <el-input v-model="editForm.sqlSentence" type="textarea" :rows="5" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="remark">
                    <el-input v-model="editForm.remark" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false"></el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
            </div>
        </el-dialog>
        <!--------------------------------------------------------------------------------------------------->


        <!--------------------------------------------------------------------------------------------------->
        <el-dialog title="" v-model="viewFormVisible" :close-on-click-modal="false" class="view-table" size="large">
            <el-tabs type="border-card">
                <el-tab-pane label="SQL"></el-tab-pane>
                <el-form label-position="right" label-width="80px" label-suffix = ":">
                    <el-row>
                        <el-col :span="24">
                            <el-form-item label="">
                                <span>{{viewForm.remark}}</span>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="24">
                            <el-form-item label="SQL">
                                <span>{{viewForm.sqlSentence}}</span>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="">
                                <span>{{viewForm.creator}}</span>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12">
                            <el-form-item label="">
                                <span>{{viewForm.modifier}}</span>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="">
                                <span>{{viewForm.createDate}}</span>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="">
                                <span>{{viewForm.updateDate}}</span>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </el-tabs>

            <div slot="footer" class="dialog-footer">
                <el-button @click.native="viewFormVisible = false"></el-button>
            </div>

        </el-dialog>
        <!--------------------------------------------------------------------------------------------------->


    </section>
</template>


<script>
    import tableHeight from '../../../js/heightUtil'
    import purviewButton from '../../../js/purviewUtil'
    import dateUtil from '../../../js/dateUtil'
    import {exportDataExcel} from '../../../js/excelUtil'
    import {pagingList, save, remove} from './sqlEngine.api'
    export default {
      data () {
        return {
          // 
          menuPurview: {
            query: true,
            add: true,
            import: true,
            export: true,
            modify: true,
            delete: true
          },
          dataList: [], // sql
          listLoading: false, // list
          tableHigth: '', // 
          sels: [], // 
          total: 0, // 
          searchClass: 'fa fa-chevron-down fa-lg', // class
          createDateRange: '', // 
          updateDateRange: '', // 
          // 
          filters: {
            pageIndex: 0,
            pageSize: 15,
            simpleDataQuery: '',
            sqlSentenceQuery: '',
            remarkQuery: '',
            createDateQuery: '',
            updateDateQuery: '',
            creatorQuery: '',
            modifierQuery: '',
            exportType: ''
          },
          // 
          seniorVisible: false,

          // 
          addFormVisible: false,
          addLoading: false,
          addForm: {
            commonFlag: 'add',
            sqlSentence: '',
            remark: '',
            creator: '',
            modifier: ''
          },
          addFormRules: {
            sqlSentence: [
              {required: true, message: 'SQL', trigger: 'blur'},
              {validator: this.validateSql, trigger: 'blur'}
            ],
            remark: [
              {required: true, message: '', trigger: 'blur'}
            ]
          },

          // 
          editLoading: false,
          editFormVisible: false,
          editForm: {
            commonFlag: 'edit',
            engineId: '',
            sqlSentence: '',
            remark: '',
            modifier: ''
          },
          editFormRules: {
            sqlSentence: [
              {required: true, message: 'SQL', trigger: 'blur'},
              {validator: this.validateSql, trigger: 'blur'}
            ],
            remark: [
              {required: true, message: '', trigger: 'blur'}
            ]
          },

          // 
          viewFormVisible: false,
          viewForm: {
            sqlSentence: '',
            remark: '',
            createDate: '',
            updateDate: '',
            creator: '',
            modifier: ''
          },
          // 
          exportDis: false

        }
      },

      methods: {
        // sql  ;
        validateSql: (rule, value, callback) => {
          if (value.indexOf(';') != -1) {
            callback(new Error(' ; '))
          } else {
            callback()
          }
        },

        // 
        formatCreateDate: function (row, column) {
          return (!row.createDate || row.createDate == '') ? '' : dateUtil.formatDate.format(new Date(row.createDate), 'yyyy-MM-dd hh:mm:ss')
        },
        formatUpdateDate: function (row, column) {
          return (!row.updateDate || row.updateDate == '') ? '' : dateUtil.formatDate.format(new Date(row.updateDate), 'yyyy-MM-dd hh:mm:ss')
        },

        /** **********************************************************************************************/
        // 
        pagingList: function () {
          if (this.createDateRange) {
            this.filters.createDateQuery = [dateUtil.formatDate.format(this.createDateRange[0], 'yyyy-MM-dd hh:mm:ss'), dateUtil.formatDate.format(this.createDateRange[1], 'yyyy-MM-dd hh:mm:ss')]
          }
          if (this.updateDateRange) {
            this.filters.updateDateQuery = [dateUtil.formatDate.format(this.updateDateRange[0], 'yyyy-MM-dd hh:mm:ss'), dateUtil.formatDate.format(this.updateDateRange[1], 'yyyy-MM-dd hh:mm:ss')]
          }
          this.listLoading = true
          this.tableHigth = tableHeight(350)
          pagingList(this.filters).then((res) => {
            if (res.error) {
              this.$message({
                showClose: true,
                message: res.error,
                type: 'error'
              })
            } else {
              this.total = res.success.total
              this.dataList = res.success.rows
            }
            this.listLoading = false
          })
        },
        // 
        handleReset () {
          this.$refs['filters'].resetFields()
          this.$refs['seniorFilters'].resetFields()
          this.filters.pageIndex = 0,
          this.filters.pageSize = 15,
          this.createDateRange = '', // 
          this.updateDateRange = '', // 
          this.pagingList()
        },
        // 
        handleSizeChange (val) {
          this.filters.pageSize = val
          this.handleCurrentChange(1)
        },
        //  
        handleCurrentChange (val) {
          if (val == 1) {
            this.filters.pageIndex = 0
          } else {
            this.filters.pageIndex = (val - 1) * (this.filters.pageSize)
          }
          this.pagingList()
        },
        // 
        handleSenior: function () {
          this.seniorVisible = !this.seniorVisible
          if (this.seniorVisible) {
            this.searchClass = 'fa fa-chevron-up fa-lg'
          } else {
            this.searchClass = 'fa fa-chevron-down fa-lg'
          }
        },
        /** **********************************************************************************************/

        /** **********************************************************************************************/
        handleView: function (index, row) {
          this.viewFormVisible = true
          this.viewForm.sqlSentence = row.sqlSentence
          this.viewForm.remark = row.remark
          this.viewForm.createDate = this.formatCreateDate(row)
          this.viewForm.updateDate = this.formatUpdateDate(row)
          this.viewForm.creator = row.creator
          this.viewForm.modifier = row.modifier
        },

        /** **********************************************************************************************/

        /** **************************************************************************************************/
        handleAdd: function () {
          if (this.$refs['addForm']) {
            this.$refs['addForm'].resetFields()
          }
          this.addFormVisible = true
        },
        // 
        addSubmit: function () {
          const currentUser = JSON.parse(sessionStorage.getItem('user'))
          this.addForm.creator = currentUser.userId
          this.addForm.modifier = currentUser.userId
          this.$refs.addForm.validate((valid) => {
            if (valid) {
              this.$confirm('', '', {}).then(() => {
                this.addLoading = true
                save(this.addForm).then((res) => {
                  this.addLoading = false
                  if (res.error) {
                    this.$message({
                      showClose: true,
                      message: data.error,
                      type: 'error'
                    })
                  } else {
                    if (res.success == 'errSql') {
                      this.$message({
                        showClose: true,
                        message: ',sql',
                        type: 'error'
                      })
                    } else {
                      this.$message({
                        showClose: true,
                        message: '',
                        type: 'success'
                      })
                    }
                  }
                  this.$refs['addForm'].resetFields()
                  this.addFormVisible = false
                  this.pagingList()
                })
              })
            }
          })
        },
        /** **************************************************************************************************/

        /** **************************************************************************************************/
        handleEdit: function (index, row) {
          this.editFormVisible = true
          this.editForm.engineId = (row.engineId)
          this.editForm.sqlSentence = (row.sqlSentence)
          this.editForm.remark = (row.remark)
        },
        // 
        editSubmit: function () {
          const currentUser = JSON.parse(sessionStorage.getItem('user'))
          this.editForm.modifier = currentUser.userId
          this.$refs.editForm.validate((valid) => {
            if (valid) {
              this.$confirm('', '', {}).then(() => {
                this.editLoading = true
                save(this.editForm).then((res) => {
                  this.editLoading = false
                  if (res.error) {
                    this.$message({
                      showClose: true,
                      message: data.error,
                      type: 'error'
                    })
                  } else {
                    if (res.success == 'errSql') {
                      this.$message({
                        showClose: true,
                        message: ',sql',
                        type: 'error'
                      })
                    } else {
                      this.$message({
                        showClose: true,
                        message: '',
                        type: 'success'
                      })
                    }
                  }
                  this.$refs['editForm'].resetFields()
                  this.editFormVisible = false
                  this.pagingList()
                })
              })
            }
          })
        },
        /** **************************************************************************************************/

        /** **************************************************************************************************/
        // 
        selsChange: function (sels) {
          this.sels = sels
        },
        handleDel: function (index, row) {
          this.$confirm('?', '', {
            type: 'warning'
          }).then(() => {
            let para = {deleteIds: row.engineId}
            remove(para).then((res) => {
              if (res.error) {
                this.$message({
                  message: data.error,
                  type: 'error'
                })
              } else {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                })
              }
              this.pagingList()
            })
          })
        },
        batchRemove: function () {
          const ids = this.sels.map(item => item.engineId).toString()
          this.$confirm('', '', {
            type: 'warning'
          }).then(() => {
            let para = {deleteIds: ids}
            remove(para).then((res) => {
              if (res.error) {
                this.$message({
                  showClose: true,
                  message: data.error,
                  type: 'error'
                })
              } else {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                })
              }

              this.pagingList()
            })
          })
        },
        /** **************************************************************************************************/

        /** **************************************************************************************************/
        handleExport (exportType) {
          this.filters.exportType = exportType
          this.exportDis = true
          exportDataExcel('/act-monitor-web/monitor/sqlEngine/exportExcel.do', this.filters)
          //  
          this.filters.exportType = ''
          this.exportDis = false
        }
        /** **************************************************************************************************/
      },

      mounted () {
        this.tableHigth = tableHeight(350)
        this.pagingList()
        let secMenuPurview = JSON.parse(sessionStorage.getItem('secMenuPurview'))
        purviewButton(secMenuPurview.sql_engine, this.menuPurview)
    }
    }
</script>

<style>
    .view-table label {
        color: #99a9bf;
    }

</style>


