<template>
    <section>



        <!---->
        <transition name="el-zoom-in-bottom">
            <el-col :span="24" class="toolbar" v-show="seniorVisible" style="padding-bottom: 0px;">

                <el-form  label-width="120px" :model="filters" ref="seniorFilters">

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="IRCS" prop = "idcIdQuery" >
                                <el-input v-model="filters.idcIdQuery" placeholder="IRCS"></el-input>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12">
                            <el-form-item label="" prop = "idcNameQuery">
                                <el-input v-model="filters.idcNameQuery" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop = "corpQuery">
                                <el-input v-model="filters.corpQuery" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12">
                            <el-form-item label="" prop = "idcZipQuery">
                                <el-input v-model="filters.idcZipQuery" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop = "idcAddQuery">
                                <el-input v-model="filters.idcAddQuery" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12">
                            <el-form-item label="" prop = "idcOfficerQuery">
                                <el-input v-model="filters.idcOfficerQuery" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="" prop = "emergencyContactQuery">
                                <el-input v-model="filters.emergencyContactQuery" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>

                        <el-col :span="12">
                            <el-form-item label="" prop = "ftpStatusQuery">
                                <el-select v-model="filters.ftpStatusQuery" placeholder="">
                                    <el-option v-for="item in options" :label="item.label" :value="item.value" :key="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="24">
                            <el-form-item label="" prop = "timeStampRange">
                                <el-date-picker v-model="timeStampRange" type="datetimerange" placeholder="" :editable="false">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </el-col>
        </transition>

        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">

            <el-form :inline="true" :model="filters" ref="filters">
                <el-form-item label="" prop = "simpleDataQuery">
                    <el-input v-model="filters.simpleDataQuery" placeholder="IRCS"></el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" v-on:click="getIrcsList" :disabled="menuPurview.query" size="small"><i class="fa fa-search fa-lg"></i> </el-button>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" v-on:click="handleSenior" :disabled="menuPurview.query" size="small"><i :class="searchClass"></i>  </el-button>
                </el-form-item>

                <el-form-item>
                    <el-button type="success" v-on:click="handleReset()" :disabled="menuPurview.query" size="small"><i class="fa fa-refresh fa-lg"></i> </el-button>
                </el-form-item>

            </el-form>

            <el-form :inline="true" >

                <el-form-item>
                    <el-button type="success" @click="handleAdd()" :disabled="menuPurview.add" icon="plus"  size="small"></el-button>
                </el-form-item>

                <el-form-item>
                    <el-button type="warning" @click="handleImport" :disabled="menuPurview.import" size="small"><i class="fa fa-cloud-upload fa-lg"></i> </el-button>
                </el-form-item>

                <el-form-item>
                    <el-dropdown @command="handleExport">
                        <el-button type="primary" :disabled="menuPurview.export" size="small" ><i class="fa fa-cloud-download fa-lg"></i> <i class="el-icon-caret-bottom el-icon--right"></i>
                        </el-button>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="0"  :disabled = "exportDis"></el-dropdown-item>
                            <el-dropdown-item command="1"  :disabled = "exportDis"></el-dropdown-item>
                            <el-dropdown-item command="-1" :disabled = "exportDis"></el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </el-form-item>
            </el-form>

        </el-col>

        <!---->
        <el-table :data="ircsTest" :height="tableHigth" v-loading="listLoading" @selection-change="selsChange"
                  highlight-current-row stripe border resizable>
            <el-table-column type="selection"  >
            </el-table-column>
            <el-table-column prop="idcid"   show-overflow-tooltip label="IRCS" label-class-name="el-tooltip" sortable>
            </el-table-column>
            <el-table-column prop="idcname"    show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="corp"    show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="idczip"   show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="idcadd"    show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>

            <el-table-column label=""  align = "center"   show-overflow-tooltip label-class-name="el-tooltip">
                <template slot-scope="scope">
                    <el-popover trigger="hover" placement="top">
                        <p>: {{ scope.row.idc_cardname }}</p>
                        <p>: {{ scope.row.idc_ID }}</p>
                        <p>: {{ scope.row.idc_tel }}</p>
                        <p>: {{ scope.row.idc_mobile }}</p>
                        <p>Email: {{ scope.row.idc_Email }}</p>
                        <div slot="reference" class="name-wrapper">
                            <el-tag>{{ scope.row.idc_name }}</el-tag>
                        </div>
                    </el-popover>
                </template>
            </el-table-column>

            <el-table-column label="" align = "center"   show-overflow-tooltip label-class-name="el-tooltip">
                <template slot-scope="scope">
                    <el-popover trigger="hover" placement="top">
                        <p>: {{ scope.row.em_cardname }}</p>
                        <p>: {{ scope.row.em_ID }}</p>
                        <p>: {{ scope.row.em_tel }}</p>
                        <p>: {{ scope.row.em_mobile }}</p>
                        <p>Email: {{ scope.row.em_Email }}</p>
                        <div slot="reference" class="name-wrapper">
                            <el-tag>{{ scope.row.em_name }}</el-tag>
                        </div>
                    </el-popover>
                </template>
            </el-table-column>

            <el-table-column prop="timestamp"   show-overflow-tooltip label="" :formatter="formatTime" sortable label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="idcispnum"    show-overflow-tooltip label="" sortable label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="usernum"   show-overflow-tooltip label="" sortable label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="ftpstatus"    show-overflow-tooltip label="" :formatter="formatState" sortable label-class-name="el-tooltip">
            </el-table-column>

            <el-table-column type="expand" label="">
                <template slot-scope="props">
                    <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="">
                            <span>{{ props.row.idcname }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.idcid }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.corp }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.idczip }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.idcadd }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.timestamp }}</span>
                        </el-form-item>
                        <el-form-item label="-">
                            <span>{{ props.row.idc_name }}</span>
                        </el-form-item>
                        <el-form-item label="-">
                            <span>{{ props.row.em_name }}</span>
                        </el-form-item>
                        <el-form-item label="-">
                            <span>{{ props.row.idc_cardname }}</span>
                        </el-form-item>
                        <el-form-item label="-">
                            <span>{{ props.row.em_cardname }}</span>
                        </el-form-item>
                        <el-form-item label="-">
                            <span>{{ props.row.idc_ID }}</span>
                        </el-form-item>
                        <el-form-item label="-">
                            <span>{{ props.row.em_ID }}</span>
                        </el-form-item>
                        <el-form-item label="-">
                            <span>{{ props.row.idc_tel }}</span>
                        </el-form-item>
                        <el-form-item label="-">
                            <span>{{ props.row.em_tel }}</span>
                        </el-form-item>
                        <el-form-item label="-">
                            <span>{{ props.row.idc_mobile }}</span>
                        </el-form-item>
                        <el-form-item label="-">
                            <span>{{ props.row.em_mobile }}</span>
                        </el-form-item>
                        <el-form-item label="-Email">
                            <span>{{ props.row.idc_Email }}</span>
                        </el-form-item>
                        <el-form-item label="-Email">
                            <span>{{ props.row.em_Email }}</span>
                        </el-form-item>
                    </el-form>
                </template>
            </el-table-column>

            <el-table-column label=""  width="100" header-align = "center" align = "center">
                <template slot-scope="scope">
                    <el-button-group>
                        <el-button size="small" type="primary" :disabled="menuPurview.modify" @click="handleEdit(scope.$index, scope.row)" icon="edit" title=""></el-button>
                        <el-button size="small" type="danger" :disabled="menuPurview.delete" @click="handleDel(scope.$index, scope.row)" icon="delete" title=""></el-button>
                    </el-button-group>
                </template>
            </el-table-column>
        </el-table>

        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0 || menuPurview.delete"></el-button>
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :page-sizes="[10,15,25,50,100]"
                    :page-size=filters.pageSize
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total"
                    style="float:right;">
            </el-pagination>
        </el-col>



        <!---->
        <el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false" size="large">
            <el-form :model="addForm" ref="addForm"  label-width="120px" :rules="addFormRules">
                <el-form-item label="IRCS" prop="idcid">
                    <el-input v-model="addForm.idcid" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="idcname">
                    <el-input v-model="addForm.idcname" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="corp">
                    <el-input v-model="addForm.corp" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="idczip">
                    <el-input v-model="addForm.idczip" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="idcadd">
                    <el-input v-model="addForm.idcadd" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-switch on-text="" off-text="" v-model="addForm.ftpstatus"></el-switch>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addFormVisible = false;"></el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
            </div>

        </el-dialog>


        <!---->
        <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false" size="large">
            <!-- label-width Control form description length -->
            <el-form :model="editForm" ref="editForm"  label-width="120px" :rules="editFormRules">
                <el-form-item label="IRCS" prop="idcid">
                    <el-input v-model="editForm.idcid"  auto-complete="off" :disabled="true" ></el-input>
                </el-form-item>
                <el-form-item label="" prop="idcname">
                    <el-input v-model="editForm.idcname"  auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="corp">
                    <el-input v-model="editForm.corp"  auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="idczip">
                    <el-input v-model="editForm.idczip" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="idcadd">
                    <el-input v-model="editForm.idcadd" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-switch on-text="" off-text="" v-model="editForm.ftpstatus"></el-switch>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false"></el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
            </div>
        </el-dialog>

        <!---->
        <el-dialog title="" v-model="importFormVisible" :close-on-click-modal="false" size="large">
            <el-upload
                    class="upload-demo"
                    action="/act-monitor-web/common/vueTest/upload.do"
                    ref="upload"
                    :headers="uploadAccept"
                    :file-list="fileList"
                    drag
                    :on-success = "importScc"
                    :on-error ="importErr"
                    :before-upload="importBefore">
                <i class="el-icon-upload"></i>
                <div class="el-upload__text"><em></em></div>
                <div class="el-upload__tip" slot="tip">: xls xlsx  (Excel 97-2003   Excel 2007)<a href="/act-monitor-web/common/vueTest/download.do"></a>
                    <br/><br/>
                    <el-button type="primary" @click="submitUpload"></el-button>
                    <el-button @click="closeImport"></el-button>
                </div>
            </el-upload>

        </el-dialog>


        <el-dialog title="" v-model="importInfoVisible" :close-on-click-modal="false" size="large">
            <div v-html="importInfo">
            </div>
        </el-dialog>


    </section>
</template>



<script>
    import dateUtil from '../../../js/dateUtil'
    import tableHeight from '../../../js/heightUtil'
    import purviewButton from '../../../js/purviewUtil'
    import {exportDataExcel} from '../../../js/excelUtil'
    import {getIrcsListPage, editIrcs, batchRemoveIrcs, exportIrcsList} from './test.api'
    export default {
      data () {
        return {
          tableHigth: '',
          menuPurview: {
            query: true,
            add: true,
            import: true,
            export: true,
            modify: true,
            delete: true
          },
          filters: {
            simpleDataQuery: '',
            idcIdQuery: '',
            idcNameQuery: '',
            corpQuery: '',
            idcZipQuery: '',
            idcAddQuery: '',
            idcOfficerQuery: '',
            emergencyContactQuery: '',
            timeStampQuery: '',
            ftpStatusQuery: '',
            pageIndex: 0,
            pageSize: 15,
            exportType: ''
          },
          options: [{
            value: '1',
            label: ''
          }, {
            value: '0',
            label: ''
          }],
          ircsTest: [],
          sels: [], // 
          border: true,
          tooltip: true,
          listLoading: false,
          total: 0,

          // 
          seniorVisible: false,
          timeStampRange: '',

          // add
          addFormVisible: false, // 
          addLoading: false,
          // 
          addForm: {
            idcid: '',
            idcname: '',
            idczip: '',
            idcadd: '',
            corp: '',
            ftpstatus: false,
            idcofficer: '1',
            emergencycontact: '2',
            commonFlag: 'add'
          },
          addFormRules: {
            idcid: [
              { required: true, message: 'IRCS', trigger: 'blur' }
            ],
            idcname: [
              { required: true, message: '', trigger: 'blur' }
            ],
            corp: [
              { required: true, message: '', trigger: 'blur' }
            ],
            idczip: [
              { required: true, message: '', trigger: 'blur' },
              { min: 6, max: 6, message: '6', trigger: 'blur' }
            ],
            idcadd: [
              { required: true, message: '', trigger: 'blur' }
            ]
          },
          //                edit
          editFormVisible: false, // 
          editLoading: false,
          editForm: {
            idcid: '',
            idcname: '',
            idczip: '',
            idcadd: '',
            corp: '',
            ftpstatus: '',
            commonFlag: 'edit'
          },
          editFormRules: {
            idcid: [
              { required: true, message: 'IRCS', trigger: 'blur' }
            ],
            idcname: [
              { required: true, message: '', trigger: 'blur' }
            ],
            corp: [
              { required: true, message: '', trigger: 'blur' }
            ],
            idczip: [
              { required: true, message: '', trigger: 'blur' },
              { min: 6, max: 6, message: '6', trigger: 'blur' }
            ],
            idcadd: [
              { required: true, message: '', trigger: 'blur' }
            ]

          },
          // 
          importFormVisible: false,
          uploadAccept: {
            accept: 'application/json'
          },
          importInfo: '',
          importInfoVisible: false,
          fileList: [],
          // 
          exportDis: false,
          searchClass: 'fa fa-chevron-down fa-lg'
          //   fa-chevron-up
        }
      },
      methods: {

        formatState: function (row, column) {
          return row.ftpstatus == true ? '' : row.ftpstatus == false ? '' : ''
        },
        formatTime: function (row, column) {
          row.timestamp = (!row.timestamp || row.timestamp == '') ? '' : dateUtil.formatDate.format(new Date(row.timestamp), 'yyyy-MM-dd hh:mm:ss')
          return row.timestamp
        },
        handleCurrentChange (val) {
          if (val == 1) {
            this.filters.pageIndex = 0
          } else {
            this.filters.pageIndex = (val - 1) * (this.filters.pageSize)
          }
          this.getIrcsList()
        },
        handleSizeChange (val) {
          this.filters.pageSize = val
          this.handleCurrentChange(1)
        },
        handleReset () {
          this.$refs['filters'].resetFields()
          this.$refs['seniorFilters'].resetFields()
          this.filters.pageIndex = 0,
          this.filters.pageSize = 15,
          this.timeStampRange = ''
          this.filters.timeStampQuery = ''
          this.getIrcsList()
        },
        // 
        handleSenior: function () {
          this.seniorVisible = !this.seniorVisible
          if (this.seniorVisible) {
            this.searchClass = 'fa fa-chevron-up fa-lg'
          } else {
            this.searchClass = 'fa fa-chevron-down fa-lg'
          }
        },
        // Paging query IRCS List
        getIrcsList () {
          this.listLoading = true
          this.tableHigth = tableHeight(350)
          if (this.timeStampRange[0]) {
            this.filters.timeStampQuery = [dateUtil.formatDate.format(this.timeStampRange[0], 'yyyy-MM-dd hh:mm:ss'), dateUtil.formatDate.format(this.timeStampRange[1], 'yyyy-MM-dd hh:mm:ss')]
          } else {
            this.filters.timeStampQuery = ''
          }
          getIrcsListPage(this.filters).then((data) => {
            if (data.error) {
              this.$message({
                showClose: true,
                message: data.error,
                type: 'error'
              })
            } else {
              this.total = data.success.total
              this.ircsTest = data.success.rows
            }
            this.listLoading = false
          })
    },
        selsChange: function (sels) {
          this.sels = sels
        },
        // 
        batchRemove: function () {
          var ids = this.sels.map(item => item.idcid).toString()
          this.$confirm('', '', {
            type: 'warning'
          }).then(() => {
            let para = { deleteIds: ids }
            batchRemoveIrcs(para).then((res) => {
              if (data.error) {
                this.$message({
                  showClose: true,
                  message: data.error,
                  type: 'error'
                })
              } else {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                })
              }
              this.getIrcsList()
            })
          })
        },

        // 
        handleDel: function (index, row) {
          this.$confirm('?', '', {
            type: 'warning'
          }).then(() => {
            let para = { deleteIds: row.idcid }
            batchRemoveIrcs(para).then((res) => {
              if (data.error) {
                this.$message({
                  message: data.error,
                  type: 'error'
                })
              } else {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                })
              }
              this.getIrcsList()
            })
          })
        },

        // 
        handleAdd: function () {
          if (this.$refs['addForm']) {
            this.$refs['addForm'].resetFields()
          }
          this.addFormVisible = true
        },
        // 
        addSubmit: function () {
          this.$refs.addForm.validate((valid) => {
            if (valid) {
              this.$confirm('', '', {}).then(() => {
                this.addLoading = true
                editIrcs(this.addForm).then((data) => {
                  this.addLoading = false
                  if (data.error) {
                    this.$message({
                      showClose: true,
                      message: data.error,
                      type: 'error'
                    })
                  } else {
                    if (res.success == 'err') {
                      this.$message({
                        showClose: true,
                        message: 'IRCS',
                        type: 'error'
                      })
                    } else {
                      this.$message({
                        showClose: true,
                        message: '',
                        type: 'success'
                      })
                    }
                  }
                  this.addFormVisible = false
                  this.getIrcsList()
                })
              })
            }
          })
        },
        // 
        handleEdit: function (index, row) {
          this.editFormVisible = true
          this.editForm.idcid = (row.idcid)
          this.editForm.idcname = (row.idcname)
          this.editForm.idczip = (row.idczip)
          this.editForm.idcadd = (row.idcadd)
          this.editForm.ftpstatus = (row.ftpstatus)
          this.editForm.corp = (row.corp)
          this.editForm.idcofficer = (row.idcofficer)
          this.editForm.emergencycontact = (row.emergencycontact)
        },
        // 
        editSubmit: function () {
          this.$refs.editForm.validate((valid) => {
            if (valid) {
              this.$confirm('', '', {}).then(() => {
                this.editLoading = true
                editIrcs(this.editForm).then((data) => {
                  this.editLoading = false
                  if (data.error) {
                    this.$message({
                      showClose: true,
                      message: data.error,
                      type: 'error'
                    })
                  } else {
                    if (res.success == 'err') {
                      this.$message({
                        showClose: true,
                        message: 'IRCS',
                        type: 'error'
                      })
                    } else {
                      this.$message({
                        showClose: true,
                        message: '',
                        type: 'success'
                      })
                    }
                  }
                  this.editFormVisible = false
                  this.getIrcsList()
                })
              })
            }
          })
        },
        // 
        handleImport: function () {
          this.importFormVisible = true
        },
        importScc: function (response, file, fileList) {
          if (response.error) {
            this.$message({
              showClose: true,
              message: response.error,
              type: 'error'
            })
          } else {
            if (response.success.uploadInfo) {
              this.importInfo = response.success.uploadInfo
              this.importInfoVisible = true
            } else {
              this.$message({
                showClose: true,
                message: '!',
                type: 'success'
              })
            }
          }
          this.fileList = []
          this.importFormVisible = false
        },
        importErr: function (err, file, fileList) {
          this.$message({
            showClose: true,
            message: '!',
            type: 'error'
          })
          this.fileList = []
          this.importFormVisible = false
        },
        importBefore: function (file) {
          const suffix = (file.name).substring((file.name).lastIndexOf('.') + 1)
          if (suffix != 'xls' && suffix != 'xlsx') {
            this.$message({
              showClose: true,
              message: 'xls xlsx',
              type: 'error'
            })
            return false
          }
          return true
        },
        submitUpload: function () {
          this.$refs.upload.submit()
        },
        closeImport: function () {
          this.importFormVisible = false
        },
        handleExport (exportType) {
          this.filters.exportType = exportType
          this.exportDis = true
          exportDataExcel('/act-monitor-web/common/vueTest/exportExcel.do', this.filters)
          //  
          this.filters.exportType = ''
          this.exportDis = false
        }
      },
      mounted () {
        this.tableHigth = tableHeight(350)
        this.getIrcsList()
        let secMenuPurview = JSON.parse(sessionStorage.getItem('secMenuPurview'))
        purviewButton(secMenuPurview.test, this.menuPurview)
    }
    }
</script>

<style>
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        margin-right: auto;
        margin-left: auto;
        margin-bottom: 0;
        width: 50%;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: auto;
        margin-left: auto;
        margin-bottom: 0;
        width: 50%;
    }

</style>


