<template>
    <section v-if="showTable">

        <!----------------------------------------------------------------------------------------------------->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">

            <el-form :inline="true" :model="filters" ref="filters">
                <el-form-item label="" prop="garbageDataType">
                    <el-select v-model="filters.garbageDataType" filterable clearable placeholder="">
                        <el-option v-for="item in options" :label="item.label" :value="item.value" :key="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" v-on:click="pagingList" :disabled="menuPurview.query" size="small"><i
                            class="fa fa-search fa-lg"></i> 


                    </el-button>
                </el-form-item>


            </el-form>
        </el-col>
        <!----------------------------------------------------------------------------------------------------->


        <!----------------------------------------------sql---------------------------------------------->
        <el-table :data="dataList" :height="tableHigth" v-loading="listLoading" highlight-current-row stripe border
                  resizable>
            <el-table-column type="index" width="60"></el-table-column>
            <el-table-column prop="remark" show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="sqlCount" show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <el-button v-if="scope.row.invalid" type="text" size="mini" disabled></el-button>
                    <el-button v-else type="text" size="mini" @click="changeShow(scope.$index, scope.row)"
                               :disabled="menuPurview.query">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!----------------------------------------------sql---------------------------------------------->


        <!----------------------------------------------------------------------------------------------------->
        <el-col :span="24" class="toolbar">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :page-sizes="[10,15,25,50,100]"
                    :page-size=filters.pageSize
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total"
                    style="float:right;">
            </el-pagination>
        </el-col>
        <!----------------------------------------------------------------------------------------------------->
    </section>


    <!--------------------------------------------------------------------------- ---------------->
    <section v-else>


        <!----------------------------------------------------------------------------------------------------->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">

            <el-form :inline="true" :model="filters" ref="filters" style="float:right;">

                <el-form-item>
                    <el-button type="danger" v-on:click="clearData()" size="small" icon="delete"
                               :disabled="clearDisabled">




                    </el-button>
                    <el-button type="success" v-on:click="changeShow()" size="small"><i class="fa fa-undo fa-lg"></i> 




                    </el-button>
                </el-form-item>

            </el-form>

        </el-col>
        <!----------------------------------------------------------------------------------------------------->


        <!---------------------------------------------------------------------------------- ------------->
        <el-table :data="garbageDataList" :height="tableHigth" v-loading="garbageListLoading" highlight-current-row
                  stripe border resizable>
            <el-table-column type="index" width="60" v-if="garbageTitle"></el-table-column>
            <el-table-column :label="item" :prop="item" v-for="(item,index) in garbageTitle" show-overflow-tooltip :key="item"
                             label-class-name="el-tooltip">
            </el-table-column>
        </el-table>
        <!--------------------------------------------------------------------------------- ------------->


        <!--------------------------------------------------------------------------------------------->
        <el-col :span="24" class="toolbar">
            <el-pagination
                    @size-change="garbageSizeChange"
                    @current-change="garbageCurrentChange"
                    :page-sizes="[10,15,25,50,100]"
                    :page-size=garbageFilters.pageSize
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="garbageTotal"
                    style="float:right;">
            </el-pagination>
        </el-col>                                                         
        <!---------------------------------------------------------------------------------------------->

    </section>
    <!--------------------------------------------------------------------------- ---------------->

</template>


<script>
    import tableHeight from '../../../js/heightUtil'
    import purviewButton from '../../../js/purviewUtil'
    import dateUtil from '../../../js/dateUtil'
    import {pagingList, pagingGarbage, clearData, initialize} from './garbageData.api'
    export default {
      data () {
        return {
          // 
          menuPurview: {
            query: true,
            add: true,
            import: true,
            export: true,
            modify: true,
            delete: true
          },
          options: [],
          showTable: true, // true false

          dataList: [], // sql
          listLoading: false, // list
          tableHigth: '', // 
          total: 0, // 
          // 
          filters: {
            pageIndex: 0,
            pageSize: 15,
            garbageDataType: ''
          },

          // garbage
          garbageFilters: {
            pageIndex: 0,
            pageSize: 15,
            reportSql: ''
          },
          garbageTotal: 0,
          garbageDataList: [], // garbagelist
          garbageTitle: [], // garbage
          garbageListLoading: false, // 
          clear: {
            garbageDelete: ''// sql
          },
          clearDisabled: false

        }
      },

      methods: {

        /** ********************************************************************************************/
        initialize: function () {
          initialize().then((res) => {
            if (res.error) {
              this.options = ''
            } else {
              this.options = res.success
            }
          })
        },
        /** ********************************************************************************************/

        /** **********************************************************************************************/
        // 
        pagingList: function () {
          this.listLoading = true
          this.tableHigth = tableHeight(350)
          pagingList(this.filters).then((res) => {
            if (res.error) {
              this.$message({
                showClose: true,
                message: res.error,
                type: 'error'
              })
            } else {
              this.total = res.success.total
              this.dataList = res.success.rows
            }
            this.listLoading = false
          })
        },
        // 
        handleSizeChange (val) {
          this.filters.pageSize = val
          this.handleCurrentChange(1)
        },
        //  
        handleCurrentChange (val) {
          if (val == 1) {
            this.filters.pageIndex = 0
          } else {
            this.filters.pageIndex = (val - 1) * (this.filters.pageSize)
          }
          this.pagingList()
        },
        /** **********************************************************************************************/

        /** **********************************************************************************************/
        changeShow: function (index, row) {
          this.showTable = !this.showTable
          if (!this.showTable) {
            this.garbageFilters.reportSql = row.seleteSql
            this.clear.garbageDelete = row.deleteSql
            if (row.sqlCount == 0) {
              this.clearDisabled = true
            } else {
              this.clearDisabled = false
            }
            this.pagingGarbage()
          } else {
            this.garbageFilters.pageIndex = 0,
            this.garbageFilters.pageSize = 15,
            this.garbageTotal = 0,
            this.garbageDataList = [], // garbagelist
            this.garbageTitle = [], // garbage
            this.clear.garbageDelete = ''
          }
        },
        /** **********************************************************************************************/

        /** *******************************************************************************************/
        pagingGarbage: function () {
          this.garbageListLoading = true
          this.tableHigth = tableHeight(350)
          pagingGarbage(this.garbageFilters).then((res) => {
            if (res.error) {
              this.$message({
                showClose: true,
                message: res.error,
                type: 'error'
              })
            } else {
              this.garbageTotal = res.success.page.total
              this.garbageDataList = res.success.page.rows
              this.garbageTitle = res.success.title
            }
            this.garbageListLoading = false
          })
        },

        //  
        garbageCurrentChange (val) {
          if (val == 1) {
            this.garbageFilters.pageIndex = 0
          } else {
            this.garbageFilters.pageIndex = (val - 1) * (this.garbageFilters.pageSize)
          }
          this.pagingGarbage()
        },

        // 
        garbageSizeChange (val) {
          this.garbageFilters.pageSize = val
          this.garbageCurrentChange(1)
        },

        /** *******************************************************************************************/

        /** *******************************************************************************************/
        clearData () {
          this.$confirm('', '', {type: 'warning'}).then(() => {
            clearData(this.clear).then((res) => {
              if (res.error) {
                this.$message({
                  showClose: true,
                  message: res.error,
                  type: 'error'
                })
              } else {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                })
              }
              this.garbageDelete = '',
              this.pagingGarbage()
            })
          })
        }
        /** *******************************************************************************************/

      },

      mounted () {
        this.tableHigth = tableHeight(350)
        this.initialize()
        this.pagingList()
        let secMenuPurview = JSON.parse(sessionStorage.getItem('secMenuPurview'))
        purviewButton(secMenuPurview.garbage_data, this.menuPurview)
    }
    }
</script>

<style>
    .demo-table-expand {
        font-size: 0;
    }

    .demo-table-expand label {
        margin-right: auto;
        margin-left: auto;
        margin-bottom: 0;
        width: 50%;
        color: #99a9bf;
    }

    .demo-table-expand .el-form-item {
        margin-right: auto;
        margin-left: auto;
        margin-bottom: 0;
        width: 50%;
    }

</style>


