
const MINH = 450 // 
const GAP = 180 // (tabs)

/**
 * content-container
 * @return {[number]} 
 */
export const CH = () => {
  let h = document.documentElement.clientHeight
  h = parseInt(h) - GAP
  h = h > MINH ? h : MINH
  return h
}



